'use client'

import React, { useEffect, useMemo, useState } from 'react'
import { useParams, useSearchParams, useRouter } from 'next/navigation'
import axios from 'axios'
import { Container } from 'reactstrap'
import { SpinnerCircular } from 'spinners-react'
import { toast } from 'react-hot-toast'
import { useTranslation } from 'react-i18next'
import type { Booking } from '../../interfaces/booking.interface'

type BookingResponse = { error: boolean; message: string; data?: Booking }

const TicketPage: React.FC = () => {
  const { t } = useTranslation()
  const router = useRouter()
  const params = useParams() as { id?: string }
  const searchParams = useSearchParams()
  const bookingId = useMemo(() => Number(params?.id), [params?.id])
  const shouldPrint = searchParams?.get('print') === '1'

  const [loading, setLoading] = useState(true)
  const [booking, setBooking] = useState<Booking | null>(null)

  useEffect(() => {
    const fetchBooking = async (): Promise<void> => {
      if (!Number.isFinite(bookingId)) {
        setLoading(false)
        return
      }
      try {
        const { data } = await axios.get<BookingResponse>(`/api/v1/bookings/booking/${bookingId}`)
        if (data?.error) {
          toast.error(data.message || 'Failed to load ticket')
          setBooking(null)
        } else {
          setBooking(data.data || null)
        }
      } catch (err: unknown) {
        if (axios.isAxiosError(err)) toast.error((err.response?.data as any)?.message || err.message || 'Failed to load ticket')
        else toast.error('An unexpected error occurred')
        setBooking(null)
      } finally {
        setLoading(false)
      }
    }
    fetchBooking()
  }, [bookingId])

  useEffect(() => {
    if (shouldPrint && !loading && booking) {
      // allow paint
      setTimeout(() => window.print(), 200)
    }
  }, [shouldPrint, loading, booking])

  const cancel = async (): Promise<void> => {
    if (!booking) return
    const ok = typeof window === 'undefined' ? true : window.confirm(t('confirm_cancel_booking') || 'Cancel this booking?')
    if (!ok) return
    try {
      const { data } = await axios.post<{ error: boolean; message: string }>(`/api/v1/bookings/cancel/${booking.id}`)
      if ((data as any)?.error) toast.error((data as any)?.message || 'Failed to cancel')
      else {
        toast.success((data as any)?.message || 'Cancelled')
        router.refresh()
      }
    } catch (err: unknown) {
      if (axios.isAxiosError(err)) toast.error((err.response?.data as any)?.message || err.message || 'Failed to cancel')
      else toast.error('An unexpected error occurred')
    }
  }

  if (loading) {
    return (
      <div className="page-content">
        <Container fluid>
          <div className="loader-outer">
            <div className="loader-inner">
              <SpinnerCircular color="#bcc014" size={30} />
            </div>
          </div>
        </Container>
      </div>
    )
  }

  if (!booking) {
    return (
      <div className="page-content">
        <Container fluid={false}>
          <div className="row">
            <div className="col-md-9 col-lg-6 col-xl-5 mx-auto my-4 box">
              <p>{t('no_booking_data') || 'No ticket found.'}</p>
            </div>
          </div>
        </Container>
      </div>
    )
  }

  return (
    <div className="page-content">
      <Container fluid={false}>
        <div className="row">
          <div className="col-md-9 col-lg-6 col-xl-5 mx-auto my-4 box" id="ticket-info">
            <h3>{t('ticket_summary') || 'Ticket'}</h3>
            <ul>
              <li>{t('ticket_number') || 'Ticket number'}: <span>{booking.ticket_number}</span></li>
              <li>{t('from') || 'From'}: <span>{booking.departure_city}</span></li>
              <li>{t('to') || 'To'}: <span>{booking.arrival_city}</span></li>
              <li>{t('journey_date') || 'Journey date'}: <span>{booking.journey_date}</span></li>
              <li>{t('time') || 'Time'}: <span>{booking.departure_time}</span></li>
              <li>{t('seat') || 'Seat'}: <span>{booking.seat || '-'}</span></li>
              <li>{t('status') || 'Status'}: <span>{booking.status || '-'}</span></li>
              <li>{t('price') || 'Price'}: <span>{booking.price} XAF</span></li>
            </ul>

            <div className="d-flex gap-2 flex-wrap mt-3">
              <button type="button" className="btn btn-primary" onClick={() => window.print()}>
                {t('print') || 'Print'}
              </button>
              <button type="button" className="btn btn-outline-danger" onClick={cancel}>
                {t('Cancel') || 'Cancel'}
              </button>
            </div>
          </div>
        </div>
      </Container>
    </div>
  )
}

export default TicketPage



